/* styles.css or your component's CSS module */
.custom-scrollbar {
  scrollbar-width: thin; /* For Firefox */
  scrollbar-color:#EB2483 #EB2483; /* For Firefox */
  
}

.custom-scrollbar::-webkit-scrollbar {
  width: 2px; /* Width of the entire scrollbar */
  border-radius: 6px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background-color: #EB2483; /* Color of the thumb */
  width: 25px;
    height: 25px;
    border-radius: 50%;
    cursor: pointer;
    border-radius: 6px;
  
}

.custom-scrollbar::-webkit-scrollbar-track {
  background-color: #fff; /* Color of the track */
  border-radius: 6px;
}
.content-div {
  margin-left: 20px; 
  margin-bottom: 10px; 
}
input[type='range'] {
    display: block;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: #EB2483;
    border-radius: 5px;
    width: 100%;
    height: 2px;
    outline: 0;
  }
  
  input[type='range']::-webkit-slider-thumb {
    -webkit-appearance: none;
    background-color: #EB2483;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    cursor: pointer;
    transition: 0.3s ease-in-out;
  }
  
  input[type='range']::-webkit-slider-thumb:active {
    transform: scale(1);
  }
  
a {

    color: #fff;
    text-decoration: none;
    line-height: 1.8;
    position: relative;
    
    text-shadow: 0px  0px  20px  black;
  }